import React from 'react'

export default function GroceryList() {
    var arr = [
        {
            "item":"dal",
            "type":"veg"
        },
        {
            "item":"fish",
            "type":"nonveg"
        },
        {
            "item":"meat",
            "type":"nonveg"
        },
        {
            "item":"rice",
            "type":"veg"
        },
    ]
  
    return (
        <div>
            {
                arr.map(item => item.type ==="veg")
            }
            
        </div>
    )
}
